      -webkit-transform: translateY(0px);
      transform: translateY(0px);
    }
  }

  @keyframes border-pulse {
    0% {
      -webkit-transform: scale(0);
      transform: scale(0);
      border-width: 20px;
    }
    40% {
      -webkit-transform: scale(1);
      transform: scale(1);
      border-width: 20px;
      margin-left: -220px;
      margin-top: -220px;
    }
    100% {
      -webkit-transform: scale(1.5);
      transform: scale(1.5);
      border-width: 0px;
      border-style: double;
      margin-left: -200px;
      margin-top: -200px;
