      transform: rotate(0deg) translate(0px, -200px);
    }
  }

  @-webkit-keyframes note-anim /* Safari and Chrome */
  {
    0% {
      opacity: 0;
      -webkit-transform: translate(0px, 50px);
      transform: translate(0px, 50px);
    }
    30% {
